export default function Avatar(props: any) {
    return (
        <>
            {props.name === "John Fang" &&
                < svg width="90" height="90" viewBox="0 0 90 90" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <circle cx="45" cy="45" r="45" fill="#F063B8" />
                    <circle cx="45" cy="45" r="45" fill="url(#pattern10)" />
                    <defs>
                        <pattern id="pattern10" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#john" transform="translate(0 -0.25) scale(0.00833333)" />
                        </pattern>
                        <image id="john" width="120" height="180" xlinkHref="data:image/jpeg;base64," />
                    </defs>
                </svg>
            }
            {props.name === "Jeny Doe" &&
                <svg width="90" height="90" viewBox="0 0 90 90" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <circle cx="45" cy="45" r="45" fill="#F063B8" />
                    <circle cx="45" cy="45" r="45" fill="url(#pattern11)" />
                    <defs>
                        <pattern id="pattern11" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#jeny" transform="translate(0 -0.125) scale(0.00694444)" />
                        </pattern>
                        <image id="jeny" width="144" height="180" xlinkHref="data:image/jpeg;base64," />
                    </defs>
                </svg>
            }
            {props.name === "William" &&
                <svg width="90" height="90" viewBox="0 0 90 90" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <circle cx="45" cy="45" r="45" fill="#F063B8" />
                    <circle cx="45" cy="45" r="45" fill="url(#pattern12)" />
                    <defs>
                        <pattern id="pattern12" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#william" transform="scale(0.00555556)" />
                        </pattern>
                        <image id="william" width="180" height="180" xlinkHref="data:image/jpeg;base64," />
                    </defs>
                </svg>
            }
        </>
    )
}