export default function HeroImage() {
    return (
        <svg width="885" height="468" viewBox="0 0 885 468" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <rect
                width="885"
                height="468"
                fill="url(#pattern1)" />
            <defs>
                <pattern id="pattern1" patternContentUnits="objectBoundingBox" width="1" height="1">
                    <use xlinkHref="#image1" transform="scale(0.00112994 0.00213675)" />
                </pattern>
                <image id="image1" width="885" height="468" xlinkHref="data:image/png;base64," />
            </defs>
        </svg>
    )
}