export default function FeatureImage(props: any) {
    return (
        <>
            {props.image === "_search" &&
                <svg width="232" height="211" viewBox="0 0 232 211" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <rect width="232" height="211" fill="url(#pattern0_search)" />
                    <defs>
                        <pattern id="pattern0_search" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#image0_search" transform="scale(0.00431034 0.00473934)" />
                        </pattern>
                        <image id="image0_search" width="232" height="211" xlinkHref="data:image/png;base64," />
                    </defs>
                </svg>
            }
            {props.image === "_access" &&
                <svg width="241" height="185" viewBox="0 0 241 185" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <rect width="241" height="185" fill="url(#pattern0_access)" />
                    <defs>
                        <pattern id="pattern0_access" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#image0_access" transform="scale(0.00414938 0.00540541)" />
                        </pattern>
                        <image id="image0_access" width="241" height="185" xlinkHref="data:image/png;base64," />
                    </defs>
                </svg>
            }
            {props.image === "_print" &&
                <svg width="245" height="181" viewBox="0 0 245 181" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <rect width="245" height="181" fill="url(#pattern0_print)" />
                    <defs>
                        <pattern id="pattern0_print" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#image0_print" transform="scale(0.00408163 0.00552486)" />
                        </pattern>
                        <image id="image0_print" width="245" height="181" xlinkHref="data:image/png;base64," />
                    </defs>
                </svg>
            }
            {props.image === "_security" &&
                <svg width="230" height="201" viewBox="0 0 230 201" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                    <rect width="230" height="201" fill="url(#pattern0_security)" />
                    <defs>
                        <pattern id="pattern0_security" patternContentUnits="objectBoundingBox" width="1" height="1">
                            <use xlinkHref="#image0_security" transform="scale(0.00434783 0.00497512)" />
                        </pattern>
                        <image id="image0_security" width="230" height="201" xlinkHref="data:image/png;base64," />
                    </defs>
                </svg>
            }
        </>
    )
}